@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
:root {
  /* Colors */
  --white: #FFFFFF;
  --gold: #E8B500;
  --gray: #C4C4C4;
  --black: #242424;
  --transparent-black: rgba(36,36,36,0.7);
  --transparent-gray: rgba(196,196,196,0.7);

  /* Font Size */
  --title-font-size: 50px;

  /* TImeline Height */
  --timeline-height: 0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  color: white;
}

html {
  scroll-behavior: smooth;
}

.highlight-letter {
  color: #E8B500;
  color: var(--gold);
  font-size: 75px;
}

.title {
  font-size: 50px;
  font-size: var(--title-font-size);
  text-align: center;
  position: relative;
  padding-bottom: 10px;
}

.title::after {
  content: '';
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 150px;
  height: 5px;
  background: #FFFFFF;
  background: var(--white);
}

.container {
  max-width: 1366px;
  margin: auto;
}


/* Animation Class */

/* Tech Stacks Section */
.tech-stacks .stack.popup-from-left {
  opacity: 1;
  margin-left: 0 !important;
}

.tech-stacks .stack.popup-from-right {
  opacity: 1;
  margin-right: 0 !important;
}

/* Project Section */
.popup {
  -webkit-transform: scale(1) !important;
          transform: scale(1) !important;
}
.App {
	background: url(/Portfolio/static/media/bg.af4ba14f.jpg);
	background-attachment: fixed; /* temporary */
}
.header {
	height: 100vh;
	display: flex;
	justify-content: space-around;
	align-items: center;
}

.top-gold-bar {
	position: absolute;
	top: 0;
	height: 10px;
	width: 100%;
	display: block;
	background: var(--gold);
}

.content {
	width: 1200px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-flow: row wrap;
}

.me {
	width: 600px;
}

.me .name {
	font-size: 50px;
	margin-bottom: -15px;
}

.me .developer {
	font-size: 35px;
}

.me .description {
	font-size: 25px;
	margin-top: 30px;
	margin-bottom: 45px;
}

.me .see-projects-button {
	display: inline-block;
	text-decoration: none;
	background: var(--gold);
	color: var(--black);
	font-size: 19px;
	padding: 12px 30px;
	border-radius: 50px;
}

.header .image-container {
	width: 400px;
	height: 400px;
	border-radius: 50%;
	overflow: hidden;
}

.image-container img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.tech-stacks {
	padding: 70px;
}

.tech-stacks .stacks {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 0 50px;
	position: relative;
	margin: 200px 0;
}
.timeline {
	display: inline-block;
	position: absolute;
	top: 20px;
	left: 50%;
	-webkit-transform-origin: top;
	        transform-origin: top;
	-webkit-transform: translateX(-50%) scaleY( var(--timeline-height) );
	        transform: translateX(-50%) scaleY( var(--timeline-height) );
	width: 5px;
	height: 1px;
	background: white;
	transition: all .3s;
}

.tech-stacks .stack {
	width: 400px;
	margin-bottom: 200px;
	opacity: 0;
	transition: all 1s;
}
.tech-stacks .stack:nth-child(even) {
	align-self: flex-start;
	margin-left: -100px;
}
.tech-stacks .stack:nth-child(odd) {
	align-self: flex-end;
	margin-right: -100px;
}
.tech-stacks .stack:last-child {
	margin-bottom: 0;
}
.tech-stacks .stack-title {
	background: var(--gold);
	text-align: center;
	padding: 10px;
	color: var(--black);
	font-size: 19px;
	font-weight: 500;
	border-radius: 30px;
}

.tech-stacks .stack .dot {
	content: '';
	position: absolute;
	left: 50%;
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	display: inline-block;
	width: 25px;
	height: 25px;
	border-radius: 50%;
	border: 5px solid var( --white );
	background: var( --gold );
}

.languages {
	padding: 10px;
	height: 150px;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	flex-flow: row wrap;
	background: var(--transparent-black);
	margin-top: 20px;
	border-radius: 30px;
}

.lang {
	display: inline-block;
	width: calc( 100% / 3 );
	text-align: center;
	font-size: 19px;
}
.my-projects .projects {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 200px 50px;
}

.project {
	width: 350px;
	background: var( --black );
	padding: 10px;
	position: relative;
	-webkit-transform: scale(0);
	        transform: scale(0);
	transition: all 1s;
}

.project::after {
	content: '';
	display: inline-block;
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 8px;
	background: var( --gold );
}

.project .image-container {
	width: 100%;
	height: 250px;
	position: relative;
}

.project .image-container:hover > img {
	object-fit: cover;
}

.project .image-container:hover > .on-hover {
	opacity: 1;
}

.project .image-container .on-hover {
	position: absolute;
	top: 0;
	left: 0;
	height: 100%;
	width: 100%;
	background: var( --transparent-gray );
	transition: all .5s;
	opacity: 0;
}

.project .image-container .on-hover a {
	background: var( --gold );
	text-decoration: none;
	color: var( --black );
	padding: 8px 20px;
	border-radius: 30px;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}

.project .name {
	font-size: 25px;
	text-align: center;
	padding: 20px 0;
}

.project .stacks {
	display: flex;
	justify-content: space-between;
	flex-flow: row wrap;
	margin: 5px 10px 30px;
}


